import ProjectSetting from '../Constants/appConfig';
import { removeUserCredential, getUserCredential } from '../lib/authentication-helper';
import axios from 'axios';

export const errStatusHandler = (resp, history, dispatch, cb, hasShowMessage = true) => {
	let { status, message, data } = resp.data;
	// let { history } = props;
	if (status === 400 || status === 401) {
		if (cb) {
			if (status >= 400) {
				cb(data && data, status);
			} else {
				cb(data && data, status);
			}
		}
		if (typeof message === "string" && hasShowMessage && dispatch) {
			// dispatch(setNewMessage({ message, type: "error", show: true }))
			alert("ERROR");
		}
	}
	if (status === 401) {
		removeUserCredential();
		if (history) history.replace("/login");
	}
};
export const generateHeader = (reduxData, reqHeader, reqBody) => {
	let headers = {};
	let body = {};

	if (reduxData) {
		// console.log("fire api", props.comHBEventVal)
		let { organiserId, eventId } = reduxData;
		let cookiesData = {};
		try {
			cookiesData = getUserCredential();
		} catch (err) {
			console.log(err);
		}
		// console.log(cookiesData)
		if (cookiesData && cookiesData.authToken) {
			headers.Authorization = `Bearer ${cookiesData.authToken}`;
			// body.access_token = cookiesData.authToken;
		}
		if (organiserId) {
			headers.organiserId = organiserId;
			// body.organiser_id = organiserId;
		}
		if (eventId) {
			headers.eventId = eventId;
			// body.event_id = eventId;
		}
	}
	if (reqHeader) {
		headers = { ...headers, ...reqHeader };
	}
	if (reqBody) {
		body = { ...body, ...reqBody }
	}

	return [headers, body]
};
const makeFakeApi = ({ data, isErr, message }) => {
	return new Promise((resolve, reject) => {
		if (isErr) reject({ status: 400, message });
		else setTimeout(() => resolve({ status: 200, data, message }), 400)
	});
}
const makeApi = (URL, method, apiStore, headers, body) => {
	let config = {
		method: method,
		url: ProjectSetting.baseAPIUrl + URL,
		headers: headers
	}

	if (apiStore) {
		if (apiStore.has(URL)) {
			let cancel = apiStore.get(URL);
			cancel();
		}
		config.cancelToken = new axios.CancelToken(function executor(cancelCb) {
			apiStore.set(URL, cancelCb);
		});
	}
	if (body) {
		config.data = body;
	}
	// console.log(body,config);
	return axios(config);
}
const upload = (URL, extraBody, formData) => {
	let { organiserId, eventId } = extraBody;
	if (organiserId) {
		if (formData.has("organiser_id")) formData.set("organiser_id", organiserId);
		else formData.append("organiser_id", organiserId);
	}
	if (eventId) {
		if (formData.has("eventId")) formData.set("eventId", eventId);
		else formData.append("eventId", eventId);
	}
	let LS = {};
	try {
		LS = getUserCredential();
	} catch (err) {
		console.log(err);
	}
	if (LS && LS.token) {
		if (formData.has("access_token")) formData.set("access_token", LS.token);
		else formData.append("access_token", LS.token);
	}
	let options = {
		headers: {},
		method: 'POST',
		body: formData
	};
	return new Promise((resolve, reject) => {
		fetch(ProjectSetting.baseURL + URL, options)
			.then(response => response.json())
			.then(res => resolve(res))
			.catch(err => reject(err));
	});
}
//common
export const POSTAPI = (isFake, URL, apiStore, headers, body) => {
	if (isFake)
		return makeFakeApi(headers);
	else
		return makeApi(URL, "POST", apiStore, headers, body);
}
export const GETAPI = (isFake, URL, apiStore, headers) => {
	if (isFake)
		return makeFakeApi(headers);
	else
		return makeApi(URL, "GET", apiStore, headers);
}
export const PUTAPI = (isFake, URL, apiStore, headers, body) => {
	if (isFake)
		return makeFakeApi(headers);
	else
		return makeApi(URL, "PUT", apiStore, headers, body);
}
// not used right now
export const UPLOADAPI = (URL, extraBody, formData) => {
	return upload(URL, extraBody, formData);
}

export const API = {
	removeAllApi: (apiStore) => {
		apiStore.forEach((cancel) => cancel());
	},
	catchHandler: (thrown, cb) => {
		if (axios.isCancel(thrown)) {
			console.log('Request canceled', thrown.message);
			return true;
		} else if (cb) {
			cb(thrown);
		} else {
			console.log(thrown);
		}
	},
	errStatusHandler: errStatusHandler,
	generateHeader: generateHeader,
	apiSuccessStatus: 200,
	unAuthorizedStatus: 403,
}