import React, { Component } from "react";
import { withRouter } from "react-router";
import {
  getUserCredential,
  removeUserCredential,
} from "../../../lib/authentication-helper";
import { GET_POST_DETAILS } from "../../../api-setup/api-endpoints";
import { API, GETAPI } from "../../../api-setup/api-helper";

class Post_Details extends React.Component {
  state = {
    user: {},
    govPost: {},
    loading: false,
  };

  componentDidMount() {
    this.makeApiCall(GET_POST_DETAILS);
  }
  hideLoader = () => {
    this.setState({ loading: false });
  };

  showLoader = () => {
    this.setState({ loading: true });
  };
  makeApiCall = (PATH) => {
    let token = getUserCredential();

    const headers = { Authorization: token.authToken };
    this.showLoader();

    GETAPI(false, PATH, null, headers, null)
      .then((data) => {
        this.hideLoader();
        console.log("DATA>", data.data);
        if (data.data.statusCode === API.apiSuccessStatus) {
          let dataObj = data.data.data;
          this.setState({
            user: dataObj.user,
            govPost: dataObj.govPost,
          });
          let posts = dataObj.govPost.map((obj) => {
            console.log("post", obj.post);
            return obj.id.toString();
          });
          console.log("posts=>", posts);
          this.props.setPostDetail(dataObj.post);
          this.props.setGovPosts(posts);
        } else if (data.data.statusCode === API.unAuthorizedStatus) {
          console.log("aaya");
          removeUserCredential();
        } else {
          console.log("aaya");

        }
      })
      .catch((err) => {
        console.log("in catch")
        this.hideLoader();
        // console.log("Register request failed")
      });
  };

  render() {
    console.log("props", this.props);
    return (
      <>
        <div className="row">
          <div className="col-lg-12">
            <div className="row float-right">
              <a
                href="#instructions"
                data-animation="fadein"
                data-plugin="custommodal"
                className="btn btn-danger btn-round  waves-effect waves-light"
                data-overlaySpeed="200"
                data-overlayColor="#36404a"
              >
                <i className="mdi mdi-information"></i>
                <span>Instructions</span>
              </a>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-lg-12 pl-5 pr-5">
            <div className=" pl-5 pr-5 qua">
              <label className="input-title mt-0" htmlFor="qualification">
                Qualification
              </label>
              <select
                disabled
                className="select2 form-control mb-3 custom-select"
              >
                <option selected>
                  {this.state.user.qualification
                    ? this.state.user.qualification
                    : null}
                </option>
              </select>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-lg-12 pl-0 pr-0">
            <div className="card mt-3 apply-online shadow-none">
              <div className="card-body pl-0 pr-0 pt-0">
                <div className="">
                  <form className="form" role="form" data-toggle="validator">
                    <div className="row col-lg-12 mt-4 mb-0 pl-0 pr-0 post-header-post">
                      <h6 className="font-14 pl-0 pr-0">
                        YOUR ARE ELIGIBLE For THE FOLLOWING POSTS. PLEASE SELECT
                        THE POST(s) YOU WANT TO APPLY.
                      </h6>
                    </div>
                    <div className="row col-lg-12 mt-2 mb-0 pl-3 pr-0">
                      <p className="font-14 pl-0 pr-0 text-pink">
                        * Candidate can apply For multiple posts. *
                      </p>
                    </div>

                    <div className="table-responsive-sm mt-0 first post-scroll">
                      <table className="table table-post mb-0">
                        <thead>
                          <tr>
                            <th scope="col">
                              <div className="checkbox checkbox-primary pt-4 pb-0 mb-0">
                                <input
                                  id="select-all"
                                  name="select-all"
                                  // onClick="toggle(this)"
                                  // value="All"
                                  checked={
                                    this.props.postDetails.posts.length ===
                                    this.props.govPosts.length
                                  }
                                  type="checkbox"
                                  data-parsley-group="block2"
                                  onChange={(e) =>
                                    this.props.handleChange(e.target.name)
                                  }
                                />
                                <label htmlFor="select-all">
                                  Select All Posts
                                </label>
                              </div>
                            </th>
                            <th scope="col">Vacancies</th>
                            <th scope="col">Pay Scale</th>
                          </tr>
                        </thead>
                        <tbody>
                          {!this.state.govPost.length ? (
                            <tr className="table-secondary">
                              <th scope="row">
                                <div className="checkbox checkbox-primary pt-2 pb-0 mb-0">
                                  <p
                                    style={{ color: "red" }}
                                    className="text-center"
                                  >
                                    No Posts To Display
                                  </p>
                                </div>
                              </th>
                            </tr>
                          ) : (
                            this.state.govPost.map((post) => {
                              return (
                                <tr className="table-secondary">
                                  <th scope="row">
                                    <div className="checkbox checkbox-primary pt-2 pb-0 mb-0">
                                      <input
                                        id={post.id}
                                        className="post"
                                        name={post.id}
                                        type="checkbox"
                                        checked={this.props.postDetails.posts.includes(
                                          post.id.toString()
                                        )}
                                        onChange={(e) =>
                                          this.props.handleChange(e.target.name)
                                        }
                                        data-parsley-group="block1"
                                      />
                                      <label htmlFor={post.id}>
                                        {post.post}
                                      </label>
                                    </div>
                                  </th>
                                  <td className="">{post.vacancies}</td>
                                  <td className="">
                                    {post.payBand} {post.minPay - post.maxPay}
                                    <br />+ GP Rs. {post.gradePay}
                                  </td>
                                </tr>
                              );
                            })
                          )}
                        </tbody>
                      </table>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default withRouter(Post_Details);
