import React, { Component } from "react";
import { getUserCredential } from "../../../lib/authentication-helper";
import { GETAPI, API } from "../../../api-setup/api-helper";
import { GET_ALL_DETAILS } from "../../../api-setup/api-endpoints";
// import { fetchDashboardData } from "../../service";

class TopUserHeader extends Component {
  state = {
    data: [],
    userdata:null,
    isLoading: true,
  };
  componentDidMount() {
    this.makeApiCall(GET_ALL_DETAILS)
  }

  makeApiCall = (PATH) => {
    let token = getUserCredential();
    const headers = { Authorization: token.authToken };

    GETAPI(false, PATH, null, headers, null)
      .then((data) => {
        this.setState({userdata:data.data.data.user})
        console.log("TopUser",data.data.data.user)
      })
      .catch(console.log("Register request failed"));
  }
   render() {
    
    return (
   
 <div className="page-wrapper-img">
 <div className="page-wrapper-img-inner">
   <div className="sidebar-user media">
     <div className="media-body">
       <h5 className="text-light text-capitalize">{this.state.userdata ? this.state.userdata.firstName +" " + this.state.userdata.lastName : null}</h5>
    <h6 className="text-light role-m">{this.state.userdata? this.state.userdata.applicationId: "N/A" }</h6>
     </div>
   </div>

   <div className="row mid-page-title">
     <div className="col-sm-12">
       <div className="page-title-box">
         <div className="float-right align-item-center mt-2">
           <div className="alert alert-info border-0" role="alert">
             <strong>Application Id:</strong> {this.state.userdata ? this.state.userdata.applicationId: null}
           </div>
         </div>
         <h4 className="page-title mb-2">
           <i className="mdi mdi-monitor-dashboard mr-2"></i>WELCOME
           TO ONLINE RECRUITMENT PROCESS
         </h4>
       </div>
     </div>
   </div>
 </div>
</div>
    );
  }
}

export default TopUserHeader;



