  hideLoader = () => {
    this.setState({ loading: false });
  };

  showLoader = () => {
    this.setState({ loading: true });
  };
  makeApiCall = (PATH) => {
    let token = getUserCredential();

    const headers = { Authorization: token.authToken };
    this.showLoader();

    GETAPI(false, PATH, null, headers, null)
      .then((data) => {
        this.hideLoader();

        console.log("Response", data);
        if (data.data.statusCode !== API.apiSuccessStatus) {
          this.setState({ ...this.state, error: data.error, loading: false });
          alert("Error");
        } else {
          console.log("post", data.data.data);
          // alert("Data added successfully");
          if (PATH === GET_ALL_DETAILS) {
            this.setState({
              caste: data.data.data.user.caste,
            });
          } else {
            if (data.data.data) {
              let {
                age_proof,
                bpl_ews,
                caste,
                emp_ex_certificate,
                pass_certificate,
                passport,
                signature,
                physically_handicap_certificate,
              } = data.data.data;
              let userCertificates = {
                age_proof,
                bpl_ews,
                caste,
                emp_ex_certificate,
                pass_certificate,
                passport,
                signature,
                physically_handicap_certificate,
              };
              this.props.setUploadDocument(userCertificates);
              this.setState({
                userCertificates,
              });
            }
          }
        }
      })
      .catch((err) => {
        this.hideLoader();
        // console.log("Register request failed")
      });
  };
