import React, { Suspense, useContext, Component } from "react";
import { AuthContext, AuthProvider } from "./context/AuthContext";

//import pages
import Index from "./Pages/Admin/Index_admin";
import DistrictReport from "./Pages/Admin/District_report_admin";
import Login from "./Pages/Admin/Login_admin";
import ScrollToTop from "./components/ScrollToTop/ScrollToTop";
import ViewApplicationDone from "./Pages/User/View_application_done";
// redux
import { createStore, applyMiddleware } from "redux";
import { Provider } from "react-redux";
import allReducers from "./Reducers/index";
import thunk from "redux-thunk";
import 'react-toastify/dist/ReactToastify.css';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
  HashRouter,
} from "react-router-dom";
import { PrivateRoute, PublicRoute, rootRender } from "./routes.jsx";
import UserDashboardApply from "./Pages/User/UserDashboardApply";
import UserDashboard from "./Pages/User/UserDashboard";
import UserLogin from "./Pages/User/Index";
import { checkIsLoggedIn } from "./lib/authentication-helper";

let store = createStore(allReducers, applyMiddleware(thunk));

const LoadingFallback = () => <div>Loading...</div>;

const UnauthenticatedRoutes = () => (
  <>
    <Route exact path="/" component={rootRender} />
    <Route exact path="/admin" component={Login} />
  </>
);

const AuthenticatedRoute = ({ children, ...rest }) => {
  const auth = useContext(AuthContext);
  return (
    <Route
      {...rest}
      render={() =>
        auth.isAuthenticated() ? <>{children}</> : <Redirect to="/" />
      }
    ></Route>
  );
};

const AppRoutes = () => {
  return (
    <>
      <Suspense fallback={<LoadingFallback />}>
        <Switch>
          <AuthenticatedRoute path="/district-wise">
            <DistrictReport />
          </AuthenticatedRoute>

          <AuthenticatedRoute path="/dashboard">
            <Index />
          </AuthenticatedRoute>
          <UnauthenticatedRoutes />
        </Switch>
      </Suspense>
    </>
  );
};

// const App = () => {
//   return (
//     <Provider store={store}>
//       <Router>
//         <ScrollToTop>
//           <AuthProvider>
//             <AppRoutes />
//           </AuthProvider>
//         </ScrollToTop>
//       </Router>
//     </Provider>
//   );
// };

const checkLogin = () => {
  let isLoggedIn = checkIsLoggedIn();
  if (isLoggedIn) {
    return <Redirect to="/userDashboard" />;
  }
  return <UserLogin />;
};

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <ScrollToTop>
            <div>
             
              <Switch>
                <PublicRoute path="/login" component={checkLogin} />
                <PrivateRoute path="/userDashboard" component={UserDashboard} />
                <PrivateRoute
                  path="/ViewApplicationDone"
                  component={ViewApplicationDone}
                />
                <PrivateRoute
                  path="/userDashboardApply"
                  component={UserDashboardApply}
                />
                <AuthProvider>
                  <AppRoutes />
                </AuthProvider>
                <Route component={() => <div>404 Error</div>} />
              </Switch>
            </div>
          </ScrollToTop>
        </Router>
      </Provider>
    );
  }
}

export default App;
