import React, { Component } from "react";

import { fetchDashboardData } from "../../service";

class LayoutTopBox extends Component {
  state = {
    data: [],
    isLoading: true,
  };

  async componentDidMount() {
    const data = await fetchDashboardData();
    this.setState({ data }, () => this.setState({ isLoading: false }));
  }

  greeting = () => {
    const myDate = new Date();
    const hrs = myDate.getHours();

    let greet = "";
    if (hrs < 12) {
      greet = "Good Morning";
    } else if (hrs >= 12 && hrs <= 17) greet = "Good Afternoon";
    else if (hrs >= 17 && hrs <= 24) greet = "Good Evening";

    return greet;
  };

  render() {
    const { data, isLoading } = this.state;

    // remove this afterwards, just put loader
    const INITIAL_VALUE = "0";
    const INITIAL_TABLE_DATA = (
      <tr>
        <td>-</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
      </tr>
    );

    return (
        <div className="row">
        <div className="col-lg-4">
          <div className="card overflow-hidden">
            <div className="card-body mb-0 bg-gradient1">
              <div className="row">
                <div className="">
                  <div className="card-icon">
                    <i className="far fa-user"></i>
                  </div>
                  <h4 className="text-white mb-0 font-16">
                    Total Registered
                  </h4>
                  <h2 className="font-weight-bold text-white">
                    {!isLoading
                      ? data[0].registered
                      : INITIAL_VALUE}
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="col-lg-4">
          <div className="card overflow-hidden">
            <div className="card-body mb-0 bg-gradient3">
              <div className="row">
                <div className="">
                  <div className="card-icon">
                    <i className="far fa-user"></i>
                  </div>
                  <h4 className="text-white mb-0 font-16">
                    Total Applied
                  </h4>
                  <h2 className="font-weight-bold text-white">
                    {!isLoading ? data[0].applied : 0}
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg-4">
          <div className="card overflow-hidden">
            <div className="card-body bg-gradient2">
              <div className="row">
                <div className="">
                  <div className="card-icon">
                    <i className="far fa-user"></i>
                  </div>
                  <h4 className="text-white mb-0 font-16">
                    Today Applied
                  </h4>
                  <h2 className="font-weight-bold text-white">
                    {!isLoading ? data[0].applied : INITIAL_VALUE}
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LayoutTopBox;
