import React, { useState, useContext } from "react";
import { Redirect } from "react-router-dom";
import { Form, Formik } from "formik";
import * as Yup from "yup";
import Layout from "../../components/Admin/layout";
import FormInput from "../../components/common/formInput";
import { AuthContext } from "../../context/AuthContext";
import { adminLogin } from "../../service";

const LoginSchema = Yup.object().shape({
  username: Yup.string().email("Enter Valid mail").required("Username is required"),
  // password: Yup.string().required("Password is required"),
  password: Yup.string().matches(
    /^.*(?=.{8,})((?=.*[!@#$%^&*()\-_=+{};:,<.>]){1})(?=.*\d)((?=.*[a-z]){1})((?=.*[A-Z]){1}).*$/,
    "Password must contain at least 8 characters, one uppercase, one number and one special case character"
  ).required("Password is required"),
});

const AdminLogin = () => {
  const authContext = useContext(AuthContext);
  const [loginSuccess, setLoginSuccess] = useState();
  const [loginError, setLoginError] = useState();
  const [redirectOnLogin, setRedirectOnLogin] = useState(false);
  const [loginLoading, setLoginLoading] = useState(false);

  const submitCredentials = async (credentials) => {
    try {
      setLoginLoading(true);
      const data = await adminLogin(credentials);
      console.log(data);

      authContext.setAuthState(data);
      setLoginSuccess(data.message);
      setLoginError(null);

      setTimeout(() => {
        setRedirectOnLogin(true);
      }, 700);
    } catch (error) {
      setLoginLoading(false);
      const { data } = error.response;
      setLoginError(data.message);
      setLoginSuccess(null);
    }
  };

  return (
    <>
      {redirectOnLogin && <Redirect to="/dashboard" />}
      <Layout>
        <div className="container-fluid">
          <div id="login-page-ors" className="row">
            <div className="login-ors-log change-pass">
              <div className="col-lg-12">
                <div className="card border mb-0 log_page shadow">
                  <div className="card-header bg-gradient4 text-white text-center">
                    <h4 className="mt-0 mb-1">Admin Login</h4>
                  </div>
                  <div className="card-body">
                    <div className="px-3">
                      <Formik
                        initialValues={{
                          email: "",
                          password: "",
                        }}
                        onSubmit={(values) => submitCredentials(values)}
                        validationSchema={LoginSchema}
                      >
                        {() => (
                          <Form>
                            {loginSuccess && <div>Success</div>}
                            {loginError && <div>Error</div>}
                            <div className="form-group font-13">
                              <label htmlFor="admin_username">User Name</label>
                              <div className="input-group mb-3">
                                <div className="input-group-prepend">
                                  <span
                                    className="input-group-text"
                                    id="basic-addon1"
                                  >
                                    <i className="mdi mdi-account-outline"></i>
                                  </span>
                                </div>
                                <FormInput
                                  ariaLabel="Username"
                                  name="username"
                                  type="text"
                                  placeholder="Enter User Name"
                                />
                              </div>
                            </div>

                            <div className="form-group font-13">
                              <label htmlFor="admin_password">Password</label>
                              <div className="input-group mb-3">
                                <div className="input-group-prepend">
                                  <span
                                    className="input-group-text"
                                    id="basic-addon2"
                                  >
                                    <i className="mdi mdi-lock-outline"></i>
                                  </span>
                                </div>
                                <FormInput
                                  ariaLabel="Password"
                                  name="password"
                                  type="password"
                                  placeholder="Enter Password"
                                />
                              </div>
                            </div>

                            <div className="form-group mb-0 row">
                              <div className="col-12 mt-2">
                                <button
                                  className="btn btn-primary btn-block waves-effect waves-light"
                                  type="submit"
                                >
                                  Login
                                  <i className="fas fa-sign-in-alt ml-1"></i>
                                </button>
                              </div>
                            </div>
                          </Form>
                        )}
                      </Formik>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    </>
  );
};

export default AdminLogin;
