import * as types from '../Constants/actionTypes'

const initialSettings = {
    isLoggedIn: false,
    loading: false,
    meta: {},
    token: null,
    permissions: null,
    status: null,
    orders: [],
    isOTP: false,
    passwordChanged: false,
    createdMembers:[]
};

const settings = (state = initialSettings, action) => {
    switch (action.type) {
        case types.AUTH_DO_LOGOUT:
            return {
                ...state,
                isLoggedIn: false,
                loading: false,
                meta: {},
                token: null
            };

        case types.REQUEST_AUTH_LOGIN:
            return {
                ...state,
                authToken: null,
                meta: {},
                loading: true,
                isLoggedIn: false
            };

        case types.RESPONSE_SEND_OTP:
            return {
                ...state,
                isOTP: true,
            };

        case types.RESPONSE_CHANGE_PASSWORD:
            return {
                ...state,
                isOTP: false,
                passwordChanged: true
            };

        case types.RESPONSE_AUTH_LOGIN:
            console.log('Action', action)
            const token = action.data && action.data.data && action.data.data.token ? action.data.data.token : null;
            const permissions = action.data && action.data.data && action.data.data.permissions ? action.data.data.permissions : null;
            const status = action.data && action.data.data && action.data.data.status ? action.data.data.status : null;
            const orders = action.data && action.data.data && action.data.data.orders ? action.data.data.orders : null;
            let createdMembers = [];
            if(action.data && action.data && action.data.data && action.data.data.createdMembers){
                createdMembers = action.data.data.createdMembers
            }
            console.log('isLoggedIn', !!token);
            return {
                ...state,
                meta: action.data,
                isLoggedIn: !!token,
                token: token,
                createdMembers,
                loading: false,
                permissions: permissions,
                status: status,
                orders: orders
            };


        default:
            return state;
    }
};

export default settings
