import * as types from '../Constants/actionTypes'
import axios from 'axios';
import AppConfig from '../Constants/appConfig';

// user register
export function registerUser(params={}){
    console.log("Add users params",params);
    return(dispatch=>{
        dispatch({type:types.REQUEST_REGISTER_USER,data:{}});
        const config = {
            headers:{
                'content-type': 'application/json',
            }
        };
        console.log('params',params);
        axios.post(AppConfig.baseAPIUrl+'/api/auth/signup',params,config)
            .then((result)=>{
                const data = result.data && result.data.data && result.data.data._id ? result.data.data : {};
                console.log("REGISTER USER: REGISTER USER: REGISTER USER result success: ", data);
                dispatch({ type: types.RESPONSE_REGISTER_USER, data: { error: false, data} });
            })
            .catch((error) => {
                let data = { error: true };
                try {
                    data['errors'] = error.response.data.errors;
                    console.log("REGISTERUSER: tryRegisterUSER: ADD REGISTER result failed: ", error);
                    console.log("Error response: ", error.response);
                    console.log("Error response body: ", error.response.body);
                }
                catch (ex) {
                    data['errors'] = [{ param: 'Network Error', msg: 'Unable to connect. Please ensure your network is connected properly.' }]
                }
                console.log("error",error.response.data.errors)
                dispatch({ type: types.RESPONSE_REGISTER_USER, data: data });
            });
    })
}