import Cookies from 'universal-cookie';
const CREDENTIAL_COOKIES_KEY = "authToken"

// set userData in cookies
export function setUserData(obj, objName) {
	const cookies = new Cookies();
	let secure = false;
	if (process.env.NODE_ENV === 'production') {
		secure = true;
	}
	cookies.set(objName, obj, { secure: secure, path: "/" });
}

// get userData in cookies
export function getUserData(objName) {
	const cookies = new Cookies();
	const userDataObj = cookies.get(objName);
	return userDataObj;
}

// delete userData in cookies
export function delUserData(objName) {
	const cookies = new Cookies();
	cookies.remove(objName);
}

export function setUserCredential(obj) {
	setUserData(obj, CREDENTIAL_COOKIES_KEY)
}
export function getUserCredential() {
	return getUserData(CREDENTIAL_COOKIES_KEY)
}
export function removeUserCredential() {
	delUserData(CREDENTIAL_COOKIES_KEY);
}

export function checkIsLoggedIn() {
	let userCredential = getUserCredential();
	console.log("userCredential",userCredential)
	return userCredential && userCredential.authToken ? true : false;
}