import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { setUserCredential } from "../../../lib/authentication-helper";
import { POSTAPI, API } from "../../../api-setup/api-helper";
import { LOGIN } from "../../../api-setup/api-endpoints";
import { LoginSchema } from "../../../models/schema";
import validateFunctions from "../../../models/validation";
import { toast } from "react-toastify";
import Loader from "../../../assets/loader/loader";

class Login extends Component {
  state = {
    loading: false,
    data: {
      email: null,
      password: null,
    },
    errorObj: {},
    isLoggedIn: false,
    loading: false,
    didRedirect: false,
  };
  hideLoader = () => {
    this.setState({ loading: false });
  };

  showLoader = () => {
    this.setState({ loading: true });
  };
  render() {
    console.log("Data", this.state.error);
    const handleChange = (e) => {
      let { name, value } = e.target;
      if (this.state.errorObj[name]) {
        let errorObj = { ...this.state.errorObj };
        errorObj[name] = "";
        console.log("in condirion");
        this.setState({ errorObj });
      }

      this.setState({
        data: {
          ...this.state.data,
          error: false,
          [name]: value,
        },
      });
    };

    const handleValidations = () => {
      let errorObj = validateFunctions.validate(LoginSchema, this.state.data);
      console.log(errorObj, "errorObj");
      if (errorObj) {
        return errorObj;
      } else {
        return false;
      }
    };
    const handleSubmit = (event) => {
      event.preventDefault();
      let errorObj = handleValidations();
      if (!errorObj) {
        this.setState({ errorObj: {} });
        let data = {
          username: this.state.data.email,
          password: this.state.data.password,
        };
        makeApiCall(data);
      } else {
        this.setState({ errorObj });
      }
    };

    const makeApiCall = (data) => {
      this.showLoader();
      POSTAPI(false, LOGIN, null, null, data)
        .then((res) => {
          console.log("Response", res);
          this.hideLoader();
          if (res.data.statusCode === API.apiSuccessStatus) {
            toast.success("LogIn Succesfully", {
              position: toast.POSITION.TOP_RIGHT,
              autoClose: 5000,
            });

            const { authToken, userData, isApplicationSubmit } = res.data.data;
            setUserCredential({ authToken: authToken });
            if (isApplicationSubmit === 1) {
              this.props.history.push("/ViewApplicationDone");
            } else {
              this.props.history.push("/userDashboard");
            }
          } else {
            this.hideLoader();
            // errorCb(res.data.message || "Some error occurred");
            // API.errStatusHandler(res, history, dispatch, () => {
            // });
          }
        })
        .catch((err) => {
          toast.error(err.response.data.message, {
            position: toast.POSITION.TOP_RIGHT,
            autoClose: 2500,
          });
          this.hideLoader();
          let errorMsg =
            err.response && err.response.data
              ? err.response.data.message
              : "Some error occurred";
          // errorCb(errorMsg);
          API.catchHandler(err, () => {});
        });
    };

    const { username, password } = this.state;
    return (
      <div className="login-ors-log">
        <div className="col-lg-12">
          <div className="card border mb-0 text-center notice">
            <div className="card-header bg-gradient4 text-white">
              <h4 className="mt-0 mb-1">Application Notice</h4>
            </div>

            <div className="card-body">
              <div className="table-responsive">
                <table className="table mb-0 ">
                  <tbody>
                    <tr>
                      <td>
                        <i className="mdi mdi-cellphone text-dark font-20"></i>
                      </td>
                      <td>0361 2652774, 255025 (11:00 AM - 4:30 PM)</td>
                    </tr>
                    <tr>
                      <td>
                        <i className="mdi mdi-whatsapp  text-custom1 font-20"></i>
                      </td>
                      <td>+91 98956 56454</td>
                    </tr>
                    <tr>
                      <td>
                        <i className="mdi mdi-email-outline text-red  font-20"></i>
                      </td>
                      <td>info@pcbassam.org</td>
                    </tr>
                    <tr>
                      <td>
                        <i className="mdi mdi-file-document-box-outline text-custom2 font-20"></i>
                      </td>
                      <td>
                        <a href="">
                          Click Here Advertisment
                          <span>
                            <i className="mdi mdi-chevron-right font-16"></i>
                          </span>
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>

          <div className="card border mt-2 log_page shadow">
            <div className="card-header bg-gradient4 text-white text-center">
              <h4 className="mt-0 mb-1">Login</h4>
            </div>
            <div className="card-body">
              <p className="text-center">Already Registered ?</p>
              <div className="px-3">
                <form
                  className="form-horizontal my-4"
                  onSubmit={(e) => handleSubmit(e)}
                >
                  <div className="form-group font-13">
                    <label htmlFor="username">
                      Registration Id/ Email Id/ Mobile Number
                    </label>
                    <div className="input-group mb-3">
                      <div className="input-group-prepend">
                        <span className="input-group-text" id="basic-addon1">
                          <i className="mdi mdi-account-outline"></i>
                        </span>
                      </div>
                      <input
                        type="text"
                        className="form-control font-13"
                        placeholder="Enter Application id/ Email Id/ Mobile Number"
                        onChange={(e) => handleChange(e)}
                        value={username}
                        name="email"
                      />
                    </div>
                    <span style={{ color: "red" }}>
                      {this.state.errorObj["email"]}
                    </span>
                  </div>

                  <div className="form-group font-13">
                    <label htmlFor="userpassword">Password</label>
                    <div className="input-group mb-3">
                      <div className="input-group-prepend">
                        <span className="input-group-text" id="basic-addon2">
                          <i className="mdi mdi-lock-outline"></i>
                        </span>
                      </div>
                      <input
                        type="password"
                        className="form-control "
                        placeholder="Enter Password"
                        onChange={(e) => handleChange(e, "password")}
                        value={password}
                        name="password"
                      />
                    </div>
                    <span style={{ color: "red" }}>
                      {this.state.errorObj["password"]}
                    </span>
                  </div>

                  <div className="form-group mb-0 row">
                    <div className="col-12 mt-2">
                      <button
                        type="submit"
                        className="btn btn-primary btn-block waves-effect waves-light"
                      >
                        Log In <i className="fas fa-sign-in-alt ml-1"></i>
                      </button>
                    </div>
                  </div>
                  <div className="form-group row mt-4">
                    <div className="col-sm-12 text-center">
                      <a href="forgot_pass.html" className="text-muted font-13">
                        <i className="mdi mdi-lock"></i> Forgot Your Password?
                      </a>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          {this.state.loading ? <Loader /> : null}
        </div>
      </div>
    );
  }
}

export default withRouter(Login);
