import React, { Component } from "react";
import Axios from "axios";
import { getUserCredential } from "../../../lib/authentication-helper";
import { GET_PREFERENCE_DETAILS, GET_POST_DETAILS } from "../../../api-setup/api-endpoints";
import { API, GETAPI } from "../../../api-setup/api-helper";
import Loader from "../../../assets/loader/loader";

export default class Preferences extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      preferenceObj: [],
      preferencePosts: [],
    };
  }

  componentDidMount() {
    this.makeApiCall(GET_PREFERENCE_DETAILS);
  }
  hideLoader = () => {
    this.setState({ loading: false });
  };

  showLoader = () => {
    this.setState({ loading: true });
  };

  makeApiCall = (PATH) => {
    let token = getUserCredential();

    const headers = { Authorization: token.authToken };
    this.showLoader();

    GETAPI(false, PATH, null, headers, null)
      .then((data) => {
        this.hideLoader();

        console.log("Response", data);
        if (data.data.statusCode === API.apiSuccessStatus) {
          let dataObj = data.data.data;
          console.log("dataObjPreference", dataObj);

          this.setState({
            preferenceObj: dataObj.userSelectedPreference,
          });
          let schema = {};

          dataObj.userSelectedPreference.map((obj) => {
            return (schema[obj.post] = [
              { method: "isRequired", errMsg: "Please select your preference" },
            ]);
          });
          console.log("schema", schema);
          this.props.setPreferenceDetailSchema(schema);
          let preference = {};
          dataObj.finalPreference.map((obj) => {
            preference[obj.post] = obj.id;
          });
          this.props.setPreference(preference);
        } else {
        }
      })
      .catch((err) => {
        this.hideLoader();
        // console.log("Register request failed")
      });
  };

  render() {
    console.log("----preference props---", this.props.preference);
    const { preference1, preference2, preference3 } = this.props.preference;

    return (
      <div>
        <div className="row">
          <div className="col-lg-12">
            <div className="row float-right">
              <a
                href="#instructions"
                data-animation="fadein"
                data-plugin="custommodal"
                className="btn btn-danger btn-round  waves-effect waves-light"
                data-overlaySpeed="200"
                data-overlayColor="#36404a"
              >
                <i className="mdi mdi-information"></i>
                <span>Instructions</span>
              </a>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-lg-12 pl-0 pr-0">
            <div className="card mt-3 apply-online shadow-none">
              <div className="card-body pl-0 pr-0 pt-0">
                <div className="">
                  <form className="form" role="form" data-toggle="validator">
                    <div className="table-responsive-sm mt-0 first">
                      <table className="table table-post mb-0">
                        <thead>
                          <tr>
                            <th scope="col">
                              <div className="pt-4 pb-0 mb-0">
                                <label for="select-all">Preferences</label>
                              </div>
                            </th>

                            <th scope="col">
                              <div className=" pt-4 pb-0 mb-0">
                                <div className="pb-0 mb-0">
                                  <div className="float-left mt-2">
                                    <label>Select Posts</label>
                                  </div>
                                  <div className="float-right">
                                    <button
                                      type="button"
                                      className="next btn btn-primary waves-effect waves-light"
                                      id="reset_pref"
                                      onClick={this.props.handleReset}
                                    >
                                      Reset Preferences
                                    </button>
                                  </div>
                                </div>
                              </div>
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.preferenceObj.length &&
                            this.state.preferenceObj.map((obj, index) => {
                              
                              return (
                                <tr className="table-secondary">
                                  <th scope="row">
                                    <div className="pt-2 pb-0 mt-2 mb-0">
                                      <label for="post1">
                                        <h5>Preference {index + 1}</h5>
                                      </label>
                                    </div>
                                  </th>

                                  <td className="">
                                    <select
                                      className="form-control custom-select po"
                                      id={obj.id}
                                      value={this.props.preference[obj.post]}
                                      name={obj.post}
                                      required
                                      onChange={(e) =>
                                        this.props.handleChangePreference(e)
                                      }
                                    >
                                      <option value="">{"Select Post"}</option>
                                      {this.state.preferenceObj.map((post) => {
                                        return (
                                          <option value={post.id}>
                                            {post.post}
                                          </option>
                                        );
                                      })}
                                    </select>
                                    <span style={{ color: "red" }}>
                                      {this.props.errorObj[obj.post]}
                                    </span>
                                  </td>
                                </tr>
                              );
                            })}
                        </tbody>
                      </table>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        {this.state.loading ? <Loader /> : null}
      </div>
    );
  }
}
