import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { removeUserCredential } from "../../lib/authentication-helper";
import { GET_ALL_DETAILS } from "../../api-setup/api-endpoints";
import { GETAPI } from "../../api-setup/api-helper";

import { getUserCredential } from "../../lib/authentication-helper";

export default function SideNav() {
  const [applicationNumber, setLoading] = useState(null,true);
  useEffect(() => {
    makeAPI(GET_ALL_DETAILS);
    setLoading(applicationNumber);
    // console.log("applicationNumber", applicationNumber);
  });
  const makeAPI = (PATH) => {
    let token = getUserCredential();
const headers = { Authorization: token.authToken };
    // this.hideLoader();
    GETAPI(false, PATH, null, headers, null)
      .then((data) => {
        // this.hideLoader();
        // console.log("Response", data.data.data.user.applicationId);
        setLoading(data.data.data.user.applicationId);
      })
      .catch(console.log("Failed"));
  };

  return (
    <div>
      <div className="left-sidenav">
        <ul className="metismenu left-sidenav-menu" id="side-nav">
          <li className="menu-title">DASHBOARD</li>

          <li>
            {applicationNumber ? (
              <Link to="/ViewApplicationDone ">
                <i className="mdi mdi-home-outline"></i>
                <span>Home</span>
              </Link>
            ) : (
              <Link to="/userDashboardApply">
                <i className="mdi mdi-home-outline"></i>
                <span>Home</span>
              </Link>
            )}
          </li>

          <li>
            <Link to="#">
              <i className="mdi mdi-lock-outline"></i>
              <span>Change Password</span>
            </Link>
          </li>
          <li
            onClick={() => {
              removeUserCredential();
            }}
          >
            <Link to="#">
              <i className="mdi mdi-power"></i>
              <span>Logout</span>
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
}
